@charset "UTf-8";

/* latest-news.css */

@import url("./delete.css");

/* import */

:root {
    /* color */
    --body-color: #fff;
    --body-color-white: #fff;
    --body-color-black: #000;
    /* background */
    --body-background: #fff;
    --body-background-white: #fff;
    --body-background-black: #000;
}

:root {
    /* title*/
    --title-box-border: #e1e1e1;
    --title-box-background: #f1f1f1;
    --title-icons-color: #4d4d4d;
    --title-icons-background: #f79530;
    /* table */
    --table-color: #121212;
    --table-background: #fff;
    /* td */
    --table-td-color: #777374;
    --table-td-border: #d7dbe0;
    /* fixed-width */
    --fixed-width-color: #121212;
}


/* white */

:root {
    /* title  */
    --title-box-border-white: #e1e1e1;
    --title-box-background-white: #f1f1f1;
    --title-icons-color-white: #4d4d4d;
    --title-icons-background-white: #f79530;
    /* table */
    --table-color-white: #121212;
    --table-background-white: #fff;
    /* td */
    --table-td-color-white: #777374;
    --table-td-border-white: #d7dbe0;
    --fixed-width-color: #121212;
    /* fixed-width */
    --fixed-width-color-white: #121212;
}


/*  black */

:root {
    /* title */
    --title-box-border-black: #1B2632;
    --title-box-background-black: #191C20;
    --title-icons-color-black: #bbc1c7;
    --title-icons-background-black: #f79530;
    /* table */
    --table-color-black: #fff;
    --table-background-black: #212529;
    /* td */
    --table-td-color-black: #fff;
    --table-td-border-black: #191C20;
    /* fixed-width */
    --fixed-width-color-black: #f79530;
}

body {
    font-family: "Microsoft YaHei", sans-serif;
    font-size: 12px;
    background: var(--body-background);
}

*,
html {
    box-sizing: border-box;
}

::before,
::after {
    margin: 0;
    padding: 0;
}

.title-box {
    width: calc(100% - 10px);
    margin: 5px;
    min-width: 1024px;
    background: var(--title-box-background);
    border: 1px solid var(--title-box-border);
    position: relative;
}

.title-icons {
    position: relative;
    color: var(--title-icons-color);
    text-indent: 14px;
    margin-left: 0;
    font-weight: 600;
    font-size: 15px;
    margin-top: 6px;
    margin-bottom: 6px;
}

.title-icons::before {
    position: absolute;
    top: 3px;
    left: 5px;
    content: "";
    display: inline-block;
    width: 4px;
    background: var(--title-icons-background);
    height: 15px;
}

.table {
    box-sizing: border-box;
    min-width: 1024px;
    font-size: 13px;
    color: var(--table-color);
    background: var(--table-background);
    width: calc(100% - 10px);
    margin: 5px;
    border-collapse: collapse;
    border-spacing: 0;
}

.table td {
    /* line-height: 36px; */
    height: 36px;
    padding: 0 5px;
    border: 1px solid var(--table-td-border);
    color: var(--table-td-color);
    font-weight: normal;
    text-align: left;
}

span {
    display: inline-block;
}


/* .span-float */

[data-float="right"] {
    float: right;
    text-align: right;
}

.fixed-width,
.fixed-min-width,
.fixed-max-width {
    line-height: 36px;
    height: 36px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: var(--fixed-width-color);
    /* background: #0f0f; */
    /* margin-bottom: -13px; */
    padding: 0 3px;
    vertical-align: middle;
}

.fixed-width {
    width: 700px;
}

.fixed-min-width {
    width: 183px;
}

.fixed-max-width {
    width: 953px;
}

@media all and (min-width: 1400px) {
    .fixed-width {
        width: 1000px;
    }
    .fixed-min-width {
        width: 203px;
    }
    .fixed-max-width {
        width: 1303px;
    }
    .table {
        min-width: 1366px;
    }
    .title-box {
        min-width: 1366px;
    }
}


