@charset "UTf-8";

/* delete.css */

:root {
    --cololr: #000;
    --default-cololr: #fff;
    --new-cololr: #0f0;
}

[data-module-box="delete"] {
    box-sizing: border-box;
    background: var(--title-box-background);
    position: absolute;
    top: 0;
    right: 0;
    width: 55px;
    /* margin-right: 3px; */
    height: 29px;
    line-height: 29px;
}

[data-delete-span="delete-span"] {
    box-sizing: border-box;
    text-indent: 25px;
    font-weight: normal;
    font-size: 12px;
    width: 55px;
    height: 28px;
    line-height: 28px;
    margin-top: -3px;
    vertical-align: middle;
    cursor: pointer;
    color: var(--title-icons-color);
    background: url(../imgs/delete-icon.png) 3px 6px var(--title-box-background) no-repeat;
    /* background: url(../imgs/delete-icon.png) 3px 6px #fff no-repeat; */
}

[data-delete-span="delete-span"]:hover {
    color: red;
}


